#bigContainer{
    justify-content: space-around;
    display: flex;
    flex-wrap: wrap;
}
.Cardcontainer{
    padding: 0;
    width: 280px;
    height: auto;
    position: relative;
    transition: 0.3s ease-in-out;
    margin: 20px;
    border-radius: 10px;
}
.Cardcontainer>img{
    width: 280px;
    border-radius: 10px;
    height: 400px;
   
}
.cardText{
    color: #ffc436;
    font-weight: 700;
    position: absolute;
    font-size: 1.8vw;
    bottom:3vh;
    left:1vw;
    transition: 0.3s ease-in;
}
.cardText:hover{
    text-shadow: 0px 0px 10px  #fbff00a9;
}

.Cardcontainer:hover{
    box-shadow:0px 0px 20px 5px #373737;
    cursor: pointer;
}