.d-block{
height: 50vh;
width: 70%;
}
.carContainer{
    width: 70%;
    margin:20px auto;
    box-shadow:0px 0px 20px 5px rgb(195 195 195 / 76%);
}
.tileImg{
    width: 280px;
    height:300px;
    margin: 20px;
    transition: 0.2s ease;
}
.tileImg:hover{
    cursor: pointer;
    box-shadow:0px 0px 20px 5px rgb(195 195 195 / 76%);
}
.tileContainer{
    margin:50px 0px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}