.footer{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 30vh;
    background-color: rgb(37, 37, 37);
}
#links{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    box-shadow: 0px 2px 0px 0px #fff;
}

.icon{
    height: 7vh;
    width: 7vh;
    margin:20px;
    color: rgb(238, 238, 238);
    transition: 0.5s ease;
}
.icon:hover{
    cursor: pointer;
    color: blanchedalmond;
}
#name{
    color: #ffffff;
    font-weight: 700;
}