.flex
{ margin: 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

#img
{
  margin:0;
height: 100vh;
width: 100%;
z-index: -100;
}

#homeimg
{
  width:100%;
  position: relative;}

#samanti
{
  color: #a1ff3b;
  font-size: 74px;
  font-weight: 900;
  position: absolute;
  top: 40%;
  left: 5%;
  transition: 0.4s ease;
}
#samanti:hover{
  text-shadow: 0px 2px 5px #a1ff3b;
}

.homeContainer{
  display: flex;
    flex-direction: column;
}


.AboutUs,.WhyUs{
  height:100vh;

}
#about,#why
{ color:#222831;
  font-weight: 800;
  align-self: center;
  font-size: 64px;
  margin: 10px;
}


#aboutText,#whyText{
  font-size: 18px;
  color:#393e46;
  align-self: center;
}
