
.contactContainer{
    color: aliceblue;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

.subparts{
    color: #001fff;
    margin: 20px;
    width: 310px;
    height: 80vh;
    background-color: rgb(255 255 255 / 74%);
    backdrop-filter: blur(1px);
    border-radius: 20px;
    transition: 0.3s ease;
    display:flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    box-shadow: 8px 0px 30px #eeeeee, -8px 0px 30px #eeeeee;
    transition: 0.5s ease;
}
.subparts:hover{
    
    box-shadow: 8px 0px 30px #eeeeee inset, -8px 0px 30px #eeeeee inset;
}

.bi{  
      width: 10vw;
    height: 10vh;}